import React from 'react';
import smile from "../images/smile.jpg";
import { Link } from "react-router-dom";
import Cart from './Cart';
import { connect } from "react-redux";

const Bag = ({ bag }) => {

const total = bag.reduce( (total, current) => total + current.price, 0 );

  return (
    <div className="bag">
      <div className="bag_header">
        <h2>CART
          <i class="fas fa-shopping-cart"></i>
          <span>{bag.length}</span>
        </h2>
      </div>
      <div className="carts_items">
        {bag.map((product)=>(
          <Cart key={product.id} product={product}/>
        ))}
      </div>
      {
        bag.length < 1?(
          <div className="info">
        <img src={smile} alt="smile"/>
        <p>It's Empty here</p>
        <p>Start shopping to add items to your bag</p>
      </div>
        ):""
      }

      <div className="invoice">
        <div>
          <p>Item Total</p>
          <p>Delivery</p>
          <p>Total</p>
          <p><small><i>Inclusive of all taxes</i></small></p>
        </div>
        <div className="price">
          <p>#{ total }</p>
          <p>free</p>
          <p>#{total}</p>
        </div>
      </div>
      
      <div className="total">
        <p>Total</p>
        <p>UGX {'total'}</p>
      </div>
      <Link to="/checkout">
        <button>Checkout</button>
      </Link>
    </div>
  )
}

const mapStateToProps = (state) => {
  return{
    bag: state.shop.bag
  }
}

export default connect(mapStateToProps)(Bag);
