import React, { useContext } from 'react';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import { addCurrentView } from "../Redux/Chart/ChartAction";
import { addToCart } from "../Redux/Chart/ChartAction";


const Products = ({product, addCurrentView, addToCart}) => {

  // let findExistedMovie =  carts.find(o => o.id === product.id);
  // let disableButton = findExistedMovie ? true:false;

  return (
    <div className="product">
      <div className="product_box1">
        <div className="product_img" onClick={() => addCurrentView(product)}>
          <Link to="/currentview">
            <img src={product.img} alt="images"/>
          </Link>
        </div>
        <div className="product_text">
          <p>{product.name}</p>
          <p># {product.price}</p>
          <input type="submit" value="add" onClick={() => addToCart(product)}/>
        </div>
      </div>
    </div>
  )
}

const mapDispatchToProps = (dispatch) => {
  return{
    addCurrentView : (product) => dispatch(addCurrentView(product)),
    addToCart : (product) => dispatch(addToCart(product))
  }
}

export default connect(null, mapDispatchToProps)(Products);