import React from 'react';
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";
import { addToCart } from "../Redux/Chart/ChartAction";

const CurrentView = ({ currentView, addToCart }) => {

  const history = useHistory() ;
  const redirectHome = ()=> {
    addToCart(currentView);
    history.push("/temple-ad");
  }
  return (
    <div>
      <div className="product_img">
        <img src={currentView.img} alt="images"/>
      </div>
      <div className="product_text">
        <p>{currentView.name}</p>
        <p># {currentView.price}</p>
        <input 
        type="submit"
        onClick={redirectHome} 
        />
      </div>
    </div>
  )
}

const mapStateToProps = (state) => {
  return{
    currentView: state.shop.currentView
  }
}

const mapDispatchToProps = (dispatch) => {
  return{
  addToCart: (currentView)=> dispatch(addToCart(currentView))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(CurrentView);
