import { electronics } from "../../productArray";
import * as actiontypes from "./ChartTypes";
import React, { useEffect } from "react";

const initialState = {
  electronics: electronics,
  currentView: localStorage.getItem("currentView")? JSON.parse(localStorage.getItem("currentView")): [],
  bag: localStorage.getItem("bag")? JSON.parse(localStorage.getItem("bag")) : []
}

 const chartReducer = (state = initialState, action) =>{
  
  switch(action.type){
    case actiontypes.ADD_TO_CART:
      localStorage.setItem("bag", JSON.stringify(state.bag));
      const recentCart = action.payload;
      if(state.bag.id === recentCart.id){
        return{
          ...state
        }
      }else{
        return{
          ...state,
          bag: [...state.bag, action.payload]
        }
      }
      break;
    case actiontypes.ADD_TO_CURRENT:
      localStorage.setItem("currentView", JSON.stringify(action.payload));
        return{
          ...state,
          currentView : action.payload
        }
     break;
     case actiontypes.REMOVE_FROM_CART:
       return{
         ...state,
         bag: state.bag.filter(cart => cart.id !== action.payload)
       }
    default :
    return state;
  }
}

export default chartReducer;