import * as actiontypes from "./ChartTypes";


export const addToCart = (id) => {
  return{
    type: actiontypes.ADD_TO_CART,
    payload: id
  };
};

export const addCurrentView = (id) => {
  return{
    type: actiontypes.ADD_TO_CURRENT,
    payload: id
  };
};

export const removeFromCart = (id) => {
  return{
    type: actiontypes.REMOVE_FROM_CART,
    payload: id
  }
}